@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Signika&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
  background: #6E304B;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.home-component{
    background: #6E304B;
    width: 100vw;
    height: 100%;
}
.main-container{
    width: 80vw;
    height: 100vh;
    padding: 8rem 0 0 0;
    margin: 0rem auto;
    display: flex;
    justify-content: space-around;
}

.main-container img {
    height: 30rem;
}

.main-container .content {
    padding-top: 5rem;
    width: 50%;
    color: white;
  }

.main-container .content h1 {
    font-size: 2.5rem;
    font-weight: bold;
    text-transform: uppercase;
    text-align: center;
    font-family: 'Montserrat',sans-serif;
  }

.main-container .content h3 {
    text-align: center;
    font-size: 1.6rem;
    margin-top: 1.2rem;
    color: #E2AE6C;
}

.main-container .content p {
    width: 420px;
    margin: 0 auto;
    margin-top: 45px;
    text-align: justify;
    margin-bottom: 2rem;
}


   @media (max-width:1024px) {
       .home {
       flex-flow: column nowrap;
       width: 100%;
}

       .content {
           width: 80%;
           margin: 0 auto;
       }

       a {
           margin-left: 12rem;
       }

   }


@media (max-width:768px) {
    .home{
        height: 80%;
        margin-top: 0rem;
    }  
    img {
        margin-top: 0;
    }

    .content {
        width: 90vw;
        margin-bottom: 4rem;
        padding: 0;
    }
    h1 {
          font-size: 1.8rem;
      }

      h2 {
          font-size: 1.2rem;
      }

      h3 {
          font-size: 1rem;
      }

      p {
          font-size: 1rem;
          margin-bottom: 10px;
      }

      a {
          font-size: 1.2rem;
          margin-left: 34%;
      }

  }

.gallery-section{
    background: #22161C;
    padding-top: 3rem;
}

.gallery-section .gallery {
    width: 80vw;
    margin: 0 auto;
    padding: 2rem 0;
    color: white;
}

.gallery .container h1 {
    color: #A37C82;
    font-weight: bold;
}

.gallery .container h1::before {
    content: '';
    background: #E2AE6C;
    height: 5px;
    width: 270px;
    margin-left: auto;
    margin-right: auto;
    display: block;
    transform: translateY(60px);
}

.gallery .container h1::after {
    content: '';
    background: #E2AE6C;
    height: 7px;
    width: 164px;
    margin-left: auto;
    margin-right: auto;
    display: block;
    transform: translateY(6px)
}
.making-fluid {
    padding: 20px;
}
.making-fluid h4{
    margin: 1rem 0;
    font-size: 1.6rem;
    text-align: center;
    color: #E2AE6C;
}

.making-fluid img {
    border-radius: 8px;
    width: 300px;
    height: 200px;
}

.gallery .container .row {
    padding-top: 30px;
}


.gallery .btn {
    box-shadow: none;
    background: #6E304B;
    color: #E2AE6C;
    padding: 8px 25px;
    border-radius: 5px;
    border: none;
    background-image: #51F089;
}

.making-fluid p {
    text-align: justify;
    font-size: 1rem;


}

#about-us {
    padding-top: 100px;
    padding-bottom: 200px;
    background: #edf5f3;
    ;
}

.about-title {
    font-size: 30px;
    font-weight: 400;
    margin-top: 8%;

}

.about-uss ul li {
    padding: 9px;
}

.about-uss {
    padding-top: 10px;
}

.change {
    margin-top: -50px;
    margin-bottom: 100px;
}

.about-component{
    background: #6E304B;
    width: 100vw;
    height: 100%;
    padding-top: 3rem;
}

.about-component .about-us{
    width: 90vw;
    margin: 0rem auto;
    font-size:1.2rem;
    padding: 40px 80px;
}

.about-us h1{
    font-size: 3rem;
    text-transform: uppercase;
    text-align: center;
    font-weight: bold;
    color: #F2F1EF;
}

.about-us h1::before {
    content: '';
    background: #E2AE6C;
    height: 5px;
    width: 270px;
    margin-left: auto;
    margin-right: auto;
    display: block;
    transform: translateY(71px);
}

.about-us h1::after {
    content: '';
    background: #E2AE6C;
    height:9px;
    width: 164px;
    margin-left: auto;
    margin-right: auto;
    display: block;
    transform: translateY(6px)
}

.about-content{
    width: 75vw;
    height: 50vh;
    margin: 0 auto;
    display: flex;
    justify-content: space-evenly;
    margin-top: 3rem;
    color: white;
    padding-top: 2rem;
    border-bottom: 4px solid white;
}

.about-content h3 {
    font-weight: 500;
    text-align: center;
    color:#E2AE6C;
    text-transform: uppercase;
    margin-bottom: 1.4rem;
}

.about-content p {
    height: 16rem;
    text-align: justify ;
    padding:1rem 0.5rem;
    font-size: 1rem;
}

.project{
    color: white;
    margin-top: 3rem;
}

.project .title{
    width: 50rem;
    margin: 0 auto;
    display: flex;
    justify-content: space-evenly;
}

.title img{
    height: 10rem;
    animation: spinner 15s infinite linear;
}

.title  h2{
    display: flex;
    align-items: center;
    font-size: 3rem;
    color: #E2AE6C;
}
.project .project-content{
    margin-top: 2rem;
    width: 69vw;
    margin: 0 auto;
    padding-top: 2rem;
}

.project-content h3{
    color: #E2AE6C;
}

@keyframes spinner{
    from{
        transform: rotate(0deg);
    }
    to{
        transform: rotate(360deg);
    }
}

